html {
  position: relative;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: Arial, sans-serif;
}

img,
svg {
  max-width: 100%;
  max-height: 100%;
  height: auto;
}

button {
  padding: 0;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p,
figure,
fieldset {
  margin: 0;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  color: #272727;
}
body .body__wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
}

.container {
  max-width: 1230px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (max-width: 1024px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  background-color: #E8E8E8;
  padding-top: 35px;
  padding-bottom: 70px;
}
@media (max-width: 825px) {
  .header {
    padding-top: 0;
    padding-bottom: 30px;
  }
}
.header__container {
  padding: 0;
}
.header__container__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  background-color: #fff;
  border-radius: 15px;
  padding: 22px;
}
@media (max-width: 1120px) {
  .header__container__wrapper {
    gap: 20px;
  }
}
@media (max-width: 825px) {
  .header__container__wrapper {
    border-radius: 0 0 20px 20px;
    padding: 15px 20px;
  }
}
.header__link {
  z-index: 11;
}
@media (max-width: 825px) {
  .header__link {
    width: 80px;
    height: 29px;
  }
}
.header__burger {
  display: none;
  position: relative;
  width: 36px;
  height: 26px;
  border: none;
  background-color: transparent;
  z-index: 11;
}
@media (max-width: 825px) {
  .header__burger {
    display: block;
  }
}
.header__burger span {
  position: absolute;
  width: 36px;
  height: 4px;
  background-color: #272727;
  transition: transform 0.3s, opacity 0.3s, background-color 0.3s;
}
.header__burger span:nth-child(1) {
  transform: translateY(0);
}
.header__burger span:nth-child(2) {
  transform: translateY(11px);
}
.header__burger span:nth-child(3) {
  transform: translateY(22px);
}
@media (max-width: 825px) {
  .header__nav {
    visibility: hidden;
    position: absolute;
    right: -320px;
    top: 0;
    width: 320px;
    height: 331px;
    background-color: #fff;
    transition: transform 0.5s, visibility 0.5s;
    z-index: 10;
    border-radius: 0 0 20px 20px;
    padding-top: 50px;
  }
}
.header__nav__social {
  display: none;
  justify-content: space-between;
  align-items: center;
  outline: none;
  padding: 0 20px;
}
.header__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
}
@media (max-width: 1120px) {
  .header__list {
    gap: 20px;
  }
}
@media (max-width: 825px) {
  .header__list {
    flex-direction: column;
    gap: 0;
    transition: transform 0.5s;
    font-size: 16px;
    line-height: 40px;
    margin-bottom: 11px;
  }
}
.header__item__link:hover {
  color: #0041A4;
}
.header__item__link:focus {
  color: #2a70d7;
}
@media (max-width: 825px) {
  .header__tel {
    display: none;
  }
}
.header__tel__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
}
@media (max-width: 1120px) {
  .header__tel__link__number {
    display: none;
  }
}

.open .header__burger span:nth-child(1) {
  transform: translateY(11px) rotate(45deg);
}
.open .header__burger span:nth-child(2) {
  opacity: 0;
}
.open .header__burger span:nth-child(3) {
  transform: translateY(11px) rotate(-45deg);
}
.open .header__nav {
  transform: translateX(-100%);
  visibility: visible;
}
.open .header__nav__social {
  display: flex;
}

.hero {
  background-color: #E8E8E8;
  padding-bottom: 50px;
  margin-bottom: 150px;
}
@media (max-width: 1024px) {
  .hero {
    padding-bottom: 25px;
    margin-bottom: 100px;
  }
}
@media (max-width: 600px) {
  .hero {
    border-radius: 0 0 20px 20px;
    margin-bottom: 30px;
  }
}
.hero .swiper {
  width: 100%;
  height: auto;
  margin: 0 auto;
}
.hero .swiper-wrapper {
  box-sizing: content-box;
}
.hero .swiper-slide {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 600px) {
  .hero .swiper-slide {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
}
@media (max-width: 1024px) {
  .hero__flex__img {
    width: 375px;
  }
}
@media (max-width: 825px) {
  .hero__flex__img {
    width: 270px;
  }
}
@media (max-width: 600px) {
  .hero__flex__img {
    order: -1;
    width: 234px;
  }
}
.hero div .swiper-pagination {
  width: 270px;
  height: 22px;
  bottom: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1024px) {
  .hero div .swiper-pagination {
    bottom: 50px;
  }
}
@media (max-width: 825px) {
  .hero div .swiper-pagination {
    bottom: -5px;
  }
}
@media (max-width: 600px) {
  .hero div .swiper-pagination {
    bottom: 10px;
    left: calc(50% - 135px);
  }
}
.hero div .swiper-pagination::before {
  content: "01";
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
}
.hero div .swiper-pagination::after {
  content: "03";
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
}
.hero .swiper-pagination-bullet {
  background-color: #fff;
  width: 51px;
  height: 4px;
  opacity: 1;
  border-radius: 100px;
  border: none;
}
.hero .swiper-pagination-bullet-active {
  background-color: #0041A4;
}
.hero__title {
  width: 485px;
  color: #465067;
  font-size: 50px;
  line-height: 62px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 90px;
}
.hero__title:not(:first-child) {
  width: 550px;
}
@media (max-width: 1024px) {
  .hero__title {
    width: 390px;
    font-size: 40px;
    line-height: 50px;
    margin-bottom: 60px;
  }
}
@media (max-width: 825px) {
  .hero__title {
    width: 290px;
    font-size: 30px;
    line-height: 40px;
  }
}
@media (max-width: 600px) {
  .hero__title {
    width: 230px;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
  }
}
.hero__btn {
  width: 270px;
  height: 60px;
  color: #fff;
  font-weight: 700;
  border-radius: 15px;
  border: none;
  background: linear-gradient(to right, #0041A4, #00A386);
  box-shadow: 10px 10px 50px rgba(0, 221, 185, 0.2509803922);
}
@media (max-width: 600px) {
  .hero__btn {
    margin-bottom: 90px;
  }
}

.about-us {
  margin-bottom: 100px;
}
@media (max-width: 1024px) {
  .about-us {
    margin-bottom: 70px;
  }
}
@media (max-width: 768px) {
  .about-us__container {
    padding: 0;
  }
}
.about-us__block1 {
  background-image: url(../img/about-us-bg.svg);
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 0 0 30px 30px;
  padding: 30px 70px 100px;
  margin-bottom: 100px;
}
@media (max-width: 1130px) {
  .about-us__block1 {
    background-color: #E8E8E8;
