@media (max-width: 1024px) {
  .about-us__block1 {
    margin-bottom: 75px;
  }
}
@media (max-width: 768px) {
  .about-us__block1 {
    margin-bottom: 50px;
  }
}
.about-us__block1 .about-us__title {
  color: #465067;
  font-size: 45px;
  line-height: 38px;
  font-weight: 700;
  margin-bottom: 45px;
}
@media (max-width: 1024px) {
  .about-us__block1 .about-us__title {
    font-size: 35px;
    line-height: 29px;
    text-transform: uppercase;
    margin-bottom: 35px;
  }
}
@media (max-width: 768px) {
  .about-us__block1 .about-us__title {
    text-align: center;
    margin-bottom: 10px;
  }
}
.about-us__block1__flex {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}
@media (max-width: 768px) {
  .about-us__block1__flex {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
}
.about-us__block1__flex__1 {
  width: 50%;
}
@media (max-width: 768px) {
  .about-us__block1__flex__1 {
    width: 100%;
    order: 2;
  }
}
.about-us__block1__flex__2 {
  width: 50%;
}
@media (max-width: 768px) {
