.about-us__block2 .about-us__list .about-us__item .about-us__item__text {
  font-weight: 700;
  text-align: center;
}

.service {
  height: 781px;
  background-image: url(../img/service-bg.svg);
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 20px;
  padding-bottom: 30px;
  margin-bottom: 30px;
}
@media (max-width: 1230px) {
  .service {
    height: auto;
    background-color: #E8E8E8;
  }
}
@media (max-width: 768px) {
  .service {
    margin-bottom: 50px;
  }
}
.service__title {
  font-size: 45px;
  line-height: 38px;
  font-weight: 700;
  text-align: center;
  color: #465067;
  text-transform: uppercase;
  padding-top: 30px;
  margin-bottom: 50px;
}
@media (max-width: 1024px) {
  .service__title {
    font-size: 35px;
    line-height: 29px;
    margin-bottom: 35px;
  }
}
@media (max-width: 768px) {
  .service__title {
    margin-bottom: 55px;
  }
}
.service__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
}
.service__item {
  width: 270px;
  height: 500px;
  position: relative;
  transition: transform 0.3s;
  overflow: hidden;
}
.service__item:hover {
  transform: scale(1.05);
}
.service__item::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  z-index: 6;
  background-color: #73A56C;
  opacity: 0.3;
  border-radius: 20px;
}
.service__item__img__wrapper::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  z-index: 5;
  background: linear-gradient(180deg, transparent, black 90%);
  opacity: 0.95;
  border-radius: 20px;
}
.service__item__icon {
  position: absolute;
  top: 180px;
  left: calc(50% - 40px);
  z-index: 10;
}
.service__item__text {
  width: 202px;
  position: absolute;
  bottom: 128px;
  left: calc(50% - 101px);
  font-size: 20px;
  line-height: 22px;
  font-weight: 700;
  color: #fff;
  text-align: center;
  z-index: 10;
}
.service__item__arrow {
  position: absolute;
  bottom: 47px;
  left: calc(50% - 20px);
  z-index: 13;
  cursor: pointer;
}
